<template>
 <div class="box"> 
   <h3>Yemekçi Güncelle</h3>
      <form @submit.prevent="onUpdateForm">
 
          <input
            type="text"
            class="form-control"
            placeholder="Yemekçi İsmi"
            v-model="yemekci.yemekci_isim"
            required
          />
 
          
        <div class="form-check-inline">
          <input type="radio" name="yemekci_grup" value="A" v-model="yemekci.yemekci_grup" checked required> 
          <label for="inlineRadio1">A Grubu</label>
        </div>

        <div class="form-check-inline">
            <input type="radio" name="yemekci_grup" value="B" v-model="yemekci.yemekci_grup" required>
         <label for="inlineRadio2">B Grubu</label>
       </div>    
          <button class="btn btn-update">Güncelle</button>
      </form>
  </div>
</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      yemekci: {},
    };
  },
  created() {
    let dbRef = db.collection("yemekciler").doc(this.$route.params.id);
    dbRef
      .get()
      .then((doc) => {
        this.yemekci = doc.data();
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    onUpdateForm(event) {
      event.preventDefault();
      db.collection("yemekciler")
        .doc(this.$route.params.id)
        .update(this.yemekci)
        .then(() => {
          console.log("User successfully updated!");
          this.$router.push("/yemekcilist");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>